<template>
  <div class="mainWarp" style="background-color:#fafafa;">
    <router-view/>
    <div class="footer">
      <ul class="" style="width:100%;height:100%;display:flex;align-items:center;justify-content:space-around;">
        <router-link v-for="(item,index) in list" :key="index" :to="item.url">
          <li @click="currentView=item.name">
            <i :class="currentView === item.name?`${item.icon}click`:`${item.icon}default`"></i>
            <span :style="{color:currentView === item.name?'#005c9c':'#828998'}">{{item.title}}</span>
          </li>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
import bus from "@/js/bus";
export default {
  name: "home",
  created() {
    bus.$on("currentRouteName", this.setCurrentRountName);
  },
  data() {
    return {
      currentView: "TransferPayment",
      list: [
        {
          name: "TransferPayment",
          url: "/home/TransferPayment",
          icon: "agiotage-icon-",
          title: this.$t("message.home.transferPament")
        },
        {
          name: "MyAccount",
          url: "/home/MyAccount",
          icon: "myAccount-icon-",
          title: this.$t("message.home.myAccount")
        },
        {
          name: "PersonalCenter",
          url: "/home/PersonalCenter",
          icon: "personal-icon-",
          title: this.$t("message.home.personalCenter")
        }
      ]
    };
  },
  methods: {
    setCurrentRountName(name) {
      this.currentView = name;
    }
  }
};
</script>

<style lang="scss"  scoped>
.footer {
  min-height: 1rem;
  width: 100%;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  font-size: 0.24rem;
  z-index: 10;
  li {
    display: flex;
    flex-flow: column;
    align-items: center;
    margin: 0.16rem;
  }
  i {
    display: inline-block;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    width: 0.34rem;
    height: 0.34rem;
    border: none;
    margin-bottom: 0.05rem;
  }
}
</style>



